<template>
  <div id="m-QRcode">
    <div id="qrcode" ref="qrcode"></div>
    <div class="statusIndicator">
      <b class="title">:</b>
      <p :style="$route.params.auditStatus | subscribe_style_filter">
        {{ $route.params.auditStatus | subscribe_status_filter }}
      </p>
      <div
        v-if="$route.params.auditStatus === '2'"
        style="textAlign: center; margin: 20px 0"
      >
        <van-button type="info" size="mini" @click="checkFailReason"
          ></van-button
        >
      </div>
    </div>
    <div class="statusIndicator" v-if="$route.params.auditStatus === '3'">
      <b class="title">:</b>
      <p
        :style="
          subscribeInfo.transboundary_type === '0'
            ? { color: 'deepyellow' }
            : { color: 'orange' }
        "
      >
        {{ subscribeInfo.transboundary_type === "0" ? "" : "" }}
      </p>
    </div>
    <div class="statusIndicator" v-if="$route.params.auditStatus === '3'">
      <b class="title">:</b>
      <p :style="passable ? { color: 'green' } : { color: 'red' }">
        {{ subscribeInfo.begin_time }}
      </p>
      <p :style="passable ? { color: 'green' } : { color: 'red' }">-</p>
      <p :style="passable ? { color: 'green' } : { color: 'red' }">
        {{ subscribeInfo.end_time }}
      </p>
    </div>
    <div class="statusIndicator" v-if="$route.params.auditStatus === '3'">
      <b class="title">:</b>
      <p
        :style="
          subscribeInfo.goods_type === '1'
            ? { color: 'orange' }
            : subscribeInfo.goods_type === '2'
            ? { color: 'red' }
            : subscribeInfo.goods_type === '5'
            ? { color: 'orange' }
            : { color: 'green' }
        "
      >
        {{ subscribeInfo.goods_type | subscribe_goodsType_filter }}
      </p>
    </div>
    <div class="statusIndicator" v-if="$route.params.auditStatus === '3'">
      <b class="title">:</b>
      <p style="fontweight: 600">
        {{ subscribeInfo.goods_weight }}
      </p>
    </div>
    <div
      class="mask"
      v-if="
        $route.params.auditStatus !== '3' && $route.params.auditStatus !== '2'
      "
    >
      <div class="replay">
        <van-icon name="replay" /> 
      </div>
    </div>
    <div class="mask" v-else-if="$route.params.auditStatus === '2'">
      <div class="replay">
        <van-icon name="replay" /> 
      </div>
    </div>
  </div>
</template>

<script>
import QRCode from "qrcodejs2";
import { get_subscribeInfo } from "@/api/port";
import { Icon, Button, Dialog } from "vant";
import "vant/lib/icon/style";
import "vant/lib/button/style";
import "vant/lib/dialog/style";

export default {
  name: "M-QRcode",
  components: {
    [Icon.name]: Icon,
    [Button.name]: Button,
    [Dialog.name]: Dialog,
  },
  data() {
    return {
      subscribeInfo: {},
      passable: false,
    };
  },
  methods: {
    qrCode(msg, QRCode, config) {
      let qrcode = new QRCode("qrcode", {
        width: 250, //
        height: 250, //
        colorDark: config.colorDark, //
        colorLight: config.colorLight, //
        typeNumber: 1,
        correctLevel: QRCode.CorrectLevel.H, // 1QRCode.CorrectLevel.L 2QRCode.CorrectLevel.M 3QRCode.CorrectLevel.Q 4QRCode.CorrectLevel.H
      });
      qrcode.clear(); //
      qrcode.makeCode(msg, QRCode); //
    },
    checkFailReason() {
      Dialog.alert({
        title: "",  
        message: this.subscribeInfo.remark + '\n',
        theme: "round-button",
        confirmButtonColor: '#1989fa',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(() => {//    
        this.$router.replace({name: 'm-complete'})
      },() => Dialog.close());
    },
  },
  filters: {
    subscribe_status_filter: function (subscribe_status) {
      switch (subscribe_status) {
        case "0":
          return "";
        case "1":
          return "";
        case "2":
          return "";
        case "3":
          return "";
      }
    },
    subscribe_style_filter: function (subscribe_status) {
      switch (subscribe_status) {
        case "0":
          return {
            color: "yellowgreen",
            fontWeight: "700",
          };
        case "1":
          return {
            color: "orange",
            fontWeight: "700",
          };
        case "2":
          return {
            color: "red",
            fontWeight: "700",
          };
        case "3":
          return {
            color: "green",
            fontWeight: "700",
          };
      }
    },
    subscribe_goodsType_filter: function (goodsType) {
      switch (goodsType) {
        case "5":
          return "";
        case "1":
          return "";
        case "2":
          return "";
        case "3":
          return "";
        case "4":
          return "";
      }
    },
  },
  async mounted() {
    if (
      this.$route.params.auditStatus === "3" ||
      this.$route.params.auditStatus === "2"
    ) {
      // 3 
      // 2  
      let qrmsg = await get_subscribeInfo({
        subscribe_id: this.$route.params.subscribeId,
        user_id: this.$store.getters.userInfo._id,
      }); // id 
      if (qrmsg.code === "1111") {
        this.subscribeInfo = qrmsg.data.subscribeInfo;
        this.passable = qrmsg.data.passable;
        this.qrCode(JSON.stringify(qrmsg.data.subscribeInfo), QRCode, {
          colorDark: "green",
          colorLight: "#fff",
        });
      } else if (qrmsg.code === "1002") {
        this.passable = qrmsg.data.passable;
        this.subscribeInfo = qrmsg.data.subscribeInfo;
        this.qrCode(JSON.stringify(qrmsg.data.subscribeInfo), QRCode, {
          colorDark: "orange",
          colorLight: "#fff",
        });
      }
    } else {
      let default_qrmsg = ""; //  
      this.qrCode(JSON.stringify(default_qrmsg), QRCode, {
        colorDark: "red",
        colorLight: "#fff",
      });
    }
  },
};
</script>

<style scoped>
#m-QRcode {
  position: relative;
  margin: 50px auto;
  width: 275px;
  height: 275px;
}
#qrcode {
  border: 2px double rgb(25, 137, 250);
  padding: 10px;
  margin: 0 auto;
}
.statusIndicator {
  margin: 10px auto;
  font-size: 16px;
}
.statusIndicator p {
  text-align: center;
  /* color: green; */
}
.title {
  text-decoration: normal;
  font-size: 14px;
}
.mask {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(200, 200, 200, 0.96);
  top: 0;
  left: 0;
}
.replay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>